<template>
  <div id="userinput" class="boxy">
    <div class="title">Subnet Information</div>
    <input type="text" id="ip-address" placeholder="0.0.0.0" v-model="$store.state.inputAddress">
    <div class="inputHere scrollcontainer"  :ref="'parentInput'">
      <div class="inputHost" @keypress="keyHandler($event,i)" v-for="(data,i) in $store.state.inputData" :key="i" >
        <input type="number" autofocus min="1" name="host" placeholder="Total Host" v-model="data[0]" />
        <input type="text" name="id" placeholder="ID" v-model="data[1]" />
        <button class="remove" @click="$store.commit('removeInputData',i)">
          -
        </button>
        <button class="add" @click="$store.commit('addInputData',i)">
          +
        </button>
      </div>
    </div>

  </div>
</template>

<script>
export default {
  methods: {
    keyHandler(eve,i) {
      const keyFunc = [
        ['Enter', () => {
          this.$store.commit('addInputData',i)
          this.$nextTick(() => {
            const listInput = this.$refs.parentInput.querySelectorAll('.inputHost')
            listInput[i+1].querySelector('input').focus()
          })
        }]
      ]
      keyFunc.forEach(p => p[0] == eve.key && p[1]())
    },
    isLast(i) {
      return this.$store.state.inputData.length-1 == i
    },
    removeInputData(index) {
      this.inputData.splice(index,1);
    },
    addInputData() {
      this.$store.state.inputData.push([])
    }
  }
}
</script>

